import React, { useEffect } from 'react';
import './Footer.css';
import { Link } from 'react-router-dom';
import translateText from './translateText';
import { useLanguage } from './LanguageContext';


export default function Footer() {
  const { targetLanguage } = useLanguage();

  const textElements = [
    { className: 'text-42', originalText: '-   ,    ' },
    { className: 'text-43', originalText: '' },
    { className: 'text-44', originalText: '' },
    { className: 'text-45', originalText: ' ' },
    { className: 'text-46', originalText: '' },
    { className: 'text-47', originalText: '' },
  ];

  useEffect(() => {
    const handleTranslate = async () => {
      try {
        for (const element of textElements) {
          // Find the text element using querySelector
          const textElement = document.querySelector(`.${element.className}`);

          // If the text element exists
          if (textElement) {
            // If target language is English ('en'), translate the original text
            if (targetLanguage === 'en') {
              const translatedText = await translateText(element.originalText, 'en');
              // Update the element with the translated text
              textElement.textContent = translatedText;
            } else {
              // If the target language is not English, use the original text
              textElement.textContent = element.originalText;
            }
          }
        }
      } catch (error) {
        console.error('Translation error:', error);
      }
    };

    handleTranslate();
  }, [targetLanguage, textElements]);

  const handleContactsClick = (event) => {
    event.preventDefault();
    const footerElement = document.getElementById('footer');
    if (footerElement) {
      footerElement.scrollIntoView({ behavior: 'smooth' });
    }
  };

  const handleEmailClick = () => {
  window.location.href = 'mailto:Blisspu.ua@gmail.com';
};
const handleInstagramClick = () => {
  window.open('https://www.instagram.com/blisspu.ua?igsh=MXE2ZThsdzFncGozcA==', '_blank');
};

const handleTelegramClick = () => {
  window.open('https://t.me/blisspu', '_blank');
};

const handlePhoneClick = () => {
  window.location.href = 'tel:+380676546565';
};

  return (
    <div id='footer' className='main-container4'>
      <div className='wrapper4'>
        <div className='section4'>
          <span className='text4'>BLISSPU</span>
          <span className='text-42'>
            -   ,    
          </span>
        </div>
        <div className='box4'>
          <div className='img4' onClick={handleInstagramClick} style={{ cursor: 'pointer' }} />
          <div className='img-42' onClick={handlePhoneClick} style={{ cursor: 'pointer' }} />
          <div className='pic4' onClick={handleTelegramClick} style={{ cursor: 'pointer' }} />
          <div className='pic-42' onClick={handleEmailClick} style={{ cursor: 'pointer' }} />
        </div>
        <div className='section-42'>
          <Link to="/" className='text-43' style={{ textDecoration: 'none' }}></Link>
          <Link to="/all" className='text-44' style={{ textDecoration: 'none' }}></Link>
          <Link to="/about" className='text-45' style={{ textDecoration: 'none' }}> </Link>
          <Link to="/help"  className='text-46' style={{ textDecoration: 'none' }}></Link>
          <Link to="/contacts" className='text-47' style={{ textDecoration: 'none' }} onClick={handleContactsClick} ></Link>
        </div>
      </div>
    </div>
  );
}
